.AddNewJob {
  background: #ffffff;
  border: 1px solid #d5d5d5;
  width: 1430px;
  height: 665px;
}

.add-namejob {
  margin-top: 50px;
  label {
    color: #000000;
    font-weight: 500;
    font-size: 20px;
    margin-top: 25px;
    margin-left: 30px;
  }
  input {
    border: 0.25px solid #c0c0c0;
    width: 655px;
    height: 51px;
    margin-top: 15px;
    margin-left: 30px;
    padding: 20px;
  }
}

