.addjob-status {
  label {
    font-weight: 500;
    font-size: 20px;
    margin-left: 30px;
    margin-top: 25px;
  }
  .addjob-in {
    display: flex;
    gap: 50px;
    margin-left: 32px;
    input[type="radio"]:checked + label {
      color: #5d5fef;
      cursor: pointer;
    }
    label {
      color: #404040;
      font-weight: 400;
      font-size: 15px;
      margin-left: 12px;
    }
    input {
      cursor: pointer;
      height: 16px;
    }
  }
}
.add-jobpublishbtn {
  background: #5d5fef;
  border-radius: 6px;
  color: #ffffff;
  width: 177px;
  height: 51px;
  margin-left: 30px;
  font-weight: 400px;
}
.add-jobcancelbtn {
  background: white;
  border-radius: 6px;
  color: black;
  width: 177px;
  font-weight: 400px;
  height: 51px;
  border: 1px solid black;
  margin-left: 30px;
  margin-top: 80px;
}
